import { Divider } from 'antd';
import React from 'react';
import { Comments, Like } from 'react-facebook';
import { useParams } from 'react-router-dom';
import RichText from '../../components/RichText/RichText';
import Spinner from '../../components/Spinner/Spinner';
import { useContentDetail } from '../../contentful/useContentDetail';
import './DetailedContent.scss';
import { Helmet } from 'react-helmet-async';

const NewsContent: React.FC = () => {
  const { id } = useParams();
  const urlSplitted = id.split('-');
  const contentId = urlSplitted[urlSplitted.length - 1];

  const newsDetailData = useContentDetail(contentId);

  if (newsDetailData.loading) {
    return <Spinner />;
  }
  if (!newsDetailData.detail) {
    return <h1>Details not available</h1>;
  }

  return (
    <div className="newsContent">
      <Helmet>
        <title>{newsDetailData.detail?.headline} - Kerala Samajam Hamburg</title>
        <meta property="og:title" content={`${newsDetailData.detail?.headline} - Kerala Samajam Hamburg`} />
        <meta name="description" content={'Kerala Samajam Hamburg: ' + newsDetailData.detail?.headline} />
      </Helmet>
      <div className="header">
        <Divider>
          <h1>{newsDetailData.detail?.headline}</h1>
          <h3>Published on: {newsDetailData.detail?.date}</h3>
        </Divider>
      </div>
      <RichText data={newsDetailData.detail?.content} />

      <div className="fb">
        <Like href={window.location.href} colorScheme="dark" showFaces share />
        <Comments width="100%" href={window.location.href} />
      </div>
    </div>
  );
};
export default NewsContent;
