import { Card, Divider, Empty } from 'antd';
import React from 'react';
import { Helmet } from 'react-helmet-async';
import { Link } from 'react-router-dom';
import newsImage from '../../assets/images/news-announcement.jpg';
import Spinner from '../../components/Spinner/Spinner';
import { useAnnouncements } from '../../contentful/useAnnouncements';
import './Announcements.scss';

const { Meta } = Card;
const News: React.FC = () => {
  const newsContent = useAnnouncements();
  if (newsContent.loading) {
    return <Spinner />;
  }

  const announcementsArray = newsContent.announcements;
  return (
    <div className={'announcements'}>
      <Helmet>
        <title>Announcements - Kerala Samajam Hamburg</title>
        <meta property="og:title" content={'Announcements - Kerala Samajam Hamburg'} />
      </Helmet>
      <Divider>
        <h2>Announcements</h2>
      </Divider>
      {announcementsArray.map((item, index) => {
        const newsDetailLink = `/details/${item.headline.split(' ').join('-')}-${item.id}`;
        return (
          <Link to={newsDetailLink} key={index}>
            <Card
              hoverable
              cover={item.image !== undefined ? <img src={item.image} alt="" /> : <img src={newsImage} alt="" />}>
              <Meta title={item.headline} description={`Published on ${item.date}`} />
            </Card>
          </Link>
        );
      })}

      {announcementsArray.length === 0 ? <Empty description={<h1>No announcements found at the moments</h1>} /> : null}
    </div>
  );
};
export default News;
