import { useEffect, useState } from 'react';
import { CONTENTFUL_CLIENT } from './index';
import { RichTextContent, Asset } from 'contentful';

interface AnnouncementsStore {
    loading: boolean;
    announcements: {
        headline: string;
        content: RichTextContent;
        date: Date;
        image: string;
        id: string;
    }[];
}

export const useAnnouncements = (): AnnouncementsStore => {
    const [newsData, setnewsData] = useState<AnnouncementsStore>({
        loading: true,
        announcements: []
    });

    useEffect(() => {
        const query = {
            content_type: 'announcements',
        };

        type ExpectedOutput = {
            headline: string;
            content: RichTextContent;
            date: Date;
            image: Asset;
        };

        CONTENTFUL_CLIENT.getEntries<ExpectedOutput>(query)
            .then((response) => {
                const news = response.items
                    .map((item) => {
                        return {
                            headline: item.fields.headline,
                            content: item.fields.content,
                            date: item.fields.date,
                            image: item.fields.image?.fields.file.url,
                            id: item.sys.id
                        };
                    })
                setnewsData({
                    loading: false,
                    announcements: news,
                });
            });
    }, []);
    return newsData;
};