import { Asset, RichTextContent } from 'contentful';
import { useEffect, useState } from 'react';
import { CONTENTFUL_CLIENT } from './index';

interface NewsStore {
  loading: boolean;
  detail?: {
    headline: string;
    content: RichTextContent;
    date: Date;
    image: string;
    id: string;
  };
}

export const useContentDetail = (newsId: string): NewsStore => {
  const [newsData, setData] = useState<NewsStore>({
    loading: true,
    detail: undefined,
  });

  useEffect(() => {
    type ExpectedOutput = {
      headline: string;
      content: RichTextContent;
      date: Date;
      image: Asset;
    };

    CONTENTFUL_CLIENT.getEntry<ExpectedOutput>(newsId).then((item) => {
      setData({
        loading: false,
        detail: {
          headline: item.fields.headline,
          content: item.fields.content,
          date: item.fields.date,
          image: item.fields.image?.fields.file.url,
          id: item.sys.id,
        },
      });
    });
  }, [newsId]);
  return newsData;
};
